/*--------------------------------------------------------------
# Theme Colors
--------------------------------------------------------------*/
.gray-color {
  color: #4c4c4c;
}

.green-color {
  color: #72bf44;
}

.red-color {
  color: #ed1c24;
}

.black-color {
  color: #000000;
}

.bt-btn {
  background-color: var(--color-primary) !important;
  font-size: 1.3rem;
  color: var(--color-white) !important;
  border: none;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-weight: 500;
  padding: 0.8rem 2.1rem;
  text-decoration: none;
  transition: 0.2s;
}
.bt-btn:hover {
  opacity: 0.9;
}
.bt-btn:focus, .bt-btn:active, .bt-btn:focus-visible {
  outline: none;
  box-shadow: none !important;
  border: none;
}
.bt-btn.h-34 {
  height: 3.4rem;
}
.bt-btn.h-48 {
  height: 4.8rem;
}
.bt-btn.h-58 {
  height: 5.8rem;
}
.bt-btn.btn-primary {
  padding: 1.2rem 2.4rem;
  font-weight: 600;
  font-size: 1.6rem;
}
.bt-btn.p-12x16 {
  padding: 1.2rem 1.6rem;
}
.bt-btn.btn-secondary {
  background-color: var(--color-white) !important;
  color: var(--color-primary) !important;
  font-weight: 600;
  font-size: 1.8rem;
  padding: 1.6rem 3.2rem;
}

.form {
  padding: 3.2rem 4.8rem;
  background-color: var(--color-white);
  border-radius: 0.8rem;
}

.bt-form .bt-label {
  font-size: 1.2rem;
  font-weight: 700;
  line-height: 2rem;
  color: var(--color-black-3);
  margin-bottom: 0.8rem;
}
.bt-form .bt-label span {
  font-weight: 500;
  color: var(--color-red);
}
.bt-form .bt-input {
  width: 100%;
  background-color: var(--color-gray-4);
  padding: 1rem 1.6rem;
  color: var(--color-black-3);
  font-weight: 400;
  font-size: 1.4rem;
  min-height: 3.9rem;
  height: 100%;
  border: 1px solid transparent;
  margin-bottom: 1.6rem;
}
.bt-form .bt-input::-webkit-outer-spin-button, .bt-form .bt-input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.bt-form .bt-input[type=number] {
  -moz-appearance: textfield;
}
.bt-form .bt-input:focus, .bt-form .bt-input:active {
  outline: none;
}
.bt-form .bt-select {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  background-image: url("../../assets/images/icons/arrow.svg");
  background-repeat: no-repeat;
  background-position: 95%;
}
.bt-form.bt-radio {
  display: flex;
  align-items: center;
}
.bt-form.bt-radio .bt-radio-input {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
.bt-form.bt-radio .bt-radio-input:checked ~ .bt-radio-label .bt-radio-label__box {
  border-color: var(--color-black);
}
.bt-form.bt-radio .bt-radio-input:checked ~ .bt-radio-label .bt-radio-label__box--inner {
  background-color: var(--color-black);
}
.bt-form.bt-radio .bt-radio-label {
  font-size: 1.3rem;
  font-weight: 400;
  line-height: 1.95rem;
  color: var(--color-black);
  margin-bottom: 0.8rem;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.bt-form.bt-radio .bt-radio-label__box {
  position: relative;
  width: 16px;
  height: 16px;
  background-color: transparent;
  border: 2px solid var(--color-black);
  border-radius: 50%;
  padding: 7px;
}
.bt-form.bt-radio .bt-radio-label__box--inner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 8px;
  height: 8px;
  background-color: transparent;
  border-radius: 50%;
  transition: 0.2s;
}

